const FETCH = "hangperson/game/FETCH";
const FETCH_SUCCESS = "hangperson/game/FETCH_SUCCESS";
const FETCH_FAIL = "hangperson/game/FETCH_FAIL";
const UPDATE = "hangperson/game/UPDATE";
const UPDATE_SUCCESS = "hangperson/game/UPDATE_SUCESS";
const UPDATE_FAIL = "hangperson/game/UPDATE_FAIL";
const CLEAR_GAME = "hangperson/game/CLEAR_GAME";

export default (state = {}, action) => {
  switch (action.type) {
    case FETCHED_GAME:
      return action.payload.state;
    case UPDATED_GAME:
      return action.payload;
    case CLEAR_GAME:
      return {};
    default:
      return state;
  }

  return state;
};

export const fetchedGameData = (game) => {
  return {
    type: FETCHED_GAME,
    payload: game
  };
};
export const updatedGame = (game) => {
  return {
    type: UPDATED_GAME,
    payload: game
  };
};


